import { Directive, Input, OnInit } from '@angular/core';


export class LivechatWidgetDirective implements OnInit {
   public licenseId: number;
   public group: number;
   public gaVersion: string;
   public environment: string;

  ngOnInit() {
    this._injectScript();
  }

  private get _group(): string {
    return this.group ? `window.__lc.group = ${this.group};` : '';
  }

  private get _gaVersion(): string {
    return this.gaVersion
      ? `window.__lc.ga_version = '${this.gaVersion}';`
      : '';
  }

  private get _environment(): string {
    return this.environment ? `${this.environment}/` : '';
  }

  private _injectScript(): void {
    const script = document.createElement('script');
    script.type = 'text/javascript';
    script.innerHTML = `
        window.__lc = window.__lc || {};
        window.__lc.license = ${this.licenseId};
        ${this._group}
        ${this._gaVersion}
        (function() {
          var lc = document.createElement('script'); lc.type = 'text/javascript'; lc.async = true;
          lc.src = 'https://cdn.livechatinc.com/${this._environment}tracking.js';
          var s = document.getElementsByTagName('script')[0]; s.parentNode.insertBefore(lc, s);
        })();
      `;

    document
      .getElementsByTagName('head')
      .item(0)
      .appendChild(script);
  }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: 'livechat-widget'
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'licenseId': [{ type: Input },],
'group': [{ type: Input },],
'gaVersion': [{ type: Input },],
'environment': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
